.main-slide{
  height: 100vh;
  width: 100%;
  background-color: transparent;

}
.carousel-slider-wrapper{
  cursor: unset !important;
}

.slide-background-image{
    background-size: 130% !important; 
    height: 100%; 
    width: 100%; 
    position: absolute; 
    background-position: center !important;
    z-index: -1;
    top: 0; 
    left: 0;
    -webkit-filter: blur(0px);
    -moz-filter: blur(1px);
    -o-filter: blur(1px);
    -ms-filter: blur(1px);
    filter: blur(1px) contrast(-20%) brightness(-5%);
    margin: 0
}
.slide-background-image.firstSlider{
    background:   linear-gradient(rgba(54, 61, 194,0.001), rgba(54, 61, 194,0.001)), 
                  linear-gradient(rgba(0, 0, 0,0.2), rgba(0, 0, 0,0.2)),
                  radial-gradient(circle farthest-corner at 40px 50vh,#49f 20%, transparent),
                  url(/ieeesb_nitdgp.github.io/static/media/1gwkpqhk.c3bd30b4.jpg);
}

.slide-background-image.secondSlider{
    background:   linear-gradient(rgba(54, 61, 194,0.001), rgba(54, 61, 194,0.001)), 
                  linear-gradient(rgba(0, 0, 0,0.2), rgba(0, 0, 0,0.2)),
                  radial-gradient(circle farthest-corner at 40px 50vh,#49f 20%, transparent),
                  url(/ieeesb_nitdgp.github.io/static/media/main-bg-2.629e8235.webp);
}

.slide-background-image.thirdSlider{
    background:   linear-gradient(rgba(54, 61, 194,0.001), rgba(54, 61, 194,0.61)), 
