	padding-left: 4rem;
	padding-right: 4rem;
}

.image-wrapper .card-img-top{
	width: 100%;
	max-width: 100%;
}
.jumbotron-special{
	display: none;
	margin-left: -0.25rem;
	margin-right: -0.25rem;
}

.team-member{
	display: flex;
}
.team-member .display{
	font-size: 1.4em;
	font-weight: 400;
}
.team-member .card{
	align-self: stretch;
	border:none;
	padding: 0;
}
.team-member .card .card-body .card-text{
	padding-bottom: 1.2rem;
}
.links-container{
	
	display: flex;
	align-items: baseline;
	margin-bottom: -1.25rem;

}
.links-container ul{
	padding: 0.5rem !important;
	margin: 0 !important;
	margin-bottom: 0;
	margin-top: auto;
	list-style: none;
	display: flex;
	width: 100%;
	justify-content: space-around;
}
.links-container ul li{
	align-self: center;
	
}

@media only screen and (max-width: 860px){
	.team-container{
		padding-left: 2rem;
		padding-right: 2rem;
	}

}
.testinomials-container{
	padding-top: 9rem;
	padding-bottom: 2rem;
}
.person .image-wrapper-person{
	max-width: 150px;
	margin: 1rem auto;

}
.person .image-wrapper-person .person-pic{
	max-width:100%;
	margin: 0 auto;

}

.card.person{
	max-width: 800px;
	min-height: 450px;
	background: transparent;
	border: none;
	position: relative;
	z-index: 9;

}

@media only screen and (max-width: 860px){
	.test-content-container{
		padding: 0.5rem 0.9rem;
    	font-size: 12px;
	}
	.person{
		min-height: 560px !important;
	}
	
}
.collab-container{
	padding: 2rem 1rem;
}
.content-container{
	display: flex;
	align-items: center;
	padding-left: 3rem;

}
.content-container h4 p{
	font-size: 1.5em;
	font-weight: 400;
	text-transform: uppercase;

}
.logos-container{
	padding: 1rem;
	padding-right: 2rem;
}
.collab-logo-wrapper{
	min-height: 100px;
	overflow: hidden;
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 1rem 2rem;
}
.collab-logo-wrapper a img{
	-webkit-filter: saturate(0);
	        filter: saturate(0);
	opacity: 0.4;
	max-height: 100px;
	width: auto;
	max-width: 100%;
}
@media only screen and (max-width: 1030px){
	.content-container h4{
		font-size: 1.2em;
	}
}
@media only screen and (max-width: 860px){
	.content-container h4 p{
		display: block;
	}
	.content-container{
		padding: 0;
		margin: 0 auto;
		text-align: center;
		
	}
	.content-container h4{
		margin: 0 auto;
	}

}
