.contact-form-container{
	margin-top: 1.25rem;
}
.form-group{
	padding-left: .938em;
	padding-right: .938em;
}
#message{
	min-height: 130px;
	border-color: #e3e3e3;
}

#name , #mail {
	border-color: #e3e3e3;
}
.form-cover{
	display: flex;
	flex-wrap: wrap;
}
.form-cover .form-group{
	flex: 0 1 50%;
}
.form-cover .form-group:first-child{
	padding-right:  1rem;
}
.contact-content{
	margin-bottom: 2rem;
}
.contact-button{
	margin-left: 0.938em;
}
.contact-text-container{
	display: flex;
	flex-direction: column;
	justify-content: center;
	margin-top: -1rem;
	font-weight: 400;
	
}
.contact-text-container ul{
	padding-left: 2rem;
	padding-right: 2rem;
	margin-top: 2.2rem;
}
.contact-text-container li{
	padding: 1rem 0.5rem;
}

.contact-sub-heading{
	font-size: 2.2em;
}
.form-text-heading{
	padding-left: 0.938em;
	padding-right: 0.938em;
}

.contact-text-container .icon-cover{
	max-width: 50px;
	padding-right: 20px;
}
.contact-text-container .icon-cover .icon-fluid{
	width: 100%;

}
@media only screen and (max-width: 860px){
	.contact-text-container{
		margin-top: 1.5rem;
		
	}

}
#myVideo {
    position: fixed;
    top:20vh;
    left: 25vw;
    width: 50%;
    height: 50%;
}

#loaderBg{
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background-color: rgb(0, 0, 0);
}
.facad{
	margin-bottom: 4rem;
}

.facad-card{
	max-width: 350px;
	margin: 0 auto;
}

.facad-card-group{
	
	margin: 0 auto;
	margin-bottom: 2rem;
}


.side-dock{
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	display: none;
	flex-direction: row;
	flex-wrap: wrap;
	background: transparent;
	color: #fff !important;
	padding: 1rem;
	padding-left: 0;
	position: fixed;
	left: 0;
	top: 50%;
	z-index: 22;
}
.side-dock-options{
	
	text-align: left;
	display: none;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	background: #49f;

}

.image-wrapper-team{
	max-height: 250px;
	overflow: hidden;
	width: 100%;
}
.side-dock-options.active-dock{
	display: flex;
}
.side-dock button{
	background: transparent;
	border: none;
	border-radius: 0;
	padding: 0.5rem;
}
.side-dock .dock-opener{
	margin-right: -2rem;
    background: #49f;
    max-height: 3rem;
    align-self: center;
}

@media only screen and (max-width: 860px){
	.side-dock{
		display: flex;
	}
}
.footer iframe{
	width:100%; 
	border:0px solid #222; 
	background-color: #000; 
	-webkit-filter: grayscale(100%);
	-moz-filter: grayscale(100%);
	-ms-filter: grayscale(100%);
	-o-filter: grayscale(100%);
	filter: grayscale(100%);
}
.footer{
	background: #eeeeee;
	padding: 4rem 2rem 2rem;
}
.footer .footer-division {
	padding: 0 1rem;
}
.text-wrapper-about{
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	padding-right: 2rem;
	margin: 0 auto;
}
.footer .footer-division .content-group ul{
	padding-left: 5px;
}
.footer .footer-division .content-group li{
	list-style: none;
	padding: 0rem 0.5rem;
	padding-left: 0;
	display: flex;
	align-items: center;
}
.footer .footer-division .content-group li::before{
	content: "";
	width: 6px;
	height: 6px;
	display: block;
	background: #49f;
}
.footer .footer-division .content-group li a{
	text-decoration: none;
	color: unset;
	font-weight: 400;
	padding: 0.3rem 0.6rem;
	padding-bottom: 0;
	
}
.footer .footer-division .content-group li a:hover{
	color: #000000;	
	
}
.division-heading{
	font-size: 1.6em;
	padding-bottom: 0.9rem;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	font-weight: 800;
}
.division-heading::after{
	content: "";
	height: 1px;
	width: 125%;
	margin-top: 0.1rem;
	display: block;
	background: #000;
	margin-left: 0;
}
.footer-logo-container{
	max-width: 30%;
	padding-bottom: 0.5rem;
	margin-top: -1rem;
}
.footer-logo-container .logo-footer{
	max-width: 100%;
	width: 100%;
	color: #49f;
}
.footer .footer-division .socials{

}
.footer .footer-division .socials a{
	padding-left: 0 !important;
	padding-right: 1rem;
}
.footer .footer-division .logo{
	height: 20px;
	width: auto;
}
@media only screen and (max-width: 860px){
	.text-wrapper-about{
		margin-left: 0;
		padding-top: 1rem;
		padding-bottom: 1rem;
	}
}
