.testinomials-container{
	padding-top: 9rem;
	padding-bottom: 2rem;
}
.person .image-wrapper-person{
	max-width: 150px;
	margin: 1rem auto;

}
.person .image-wrapper-person .person-pic{
	max-width:100%;
	margin: 0 auto;

}

.card.person{
	max-width: 800px;
	min-height: 450px;
	background: transparent;
	border: none;
	position: relative;
	z-index: 9;

}

@media only screen and (max-width: 860px){
	.test-content-container{
		padding: 0.5rem 0.9rem;
    	font-size: 12px;
	}
	.person{
		min-height: 560px !important;
	}
	
}